<template>
  <div class="planeDesign">
    <common-title :index="6"></common-title>
    <div class="content">
      <h3 class="title"></h3>
      <div class="banner_img">
        <img src="/static/images/plane/banner_pingmian.jpg" alt="">
      </div>
      <p class="text">graphic designpage layout<br>
        
      </p>
    </div>
    <div class="type_list">
      <h3 class="title"></h3>
      <p>
        1. <br>
        2.<br>
        3.<br>
        4. <br>
        5.<br>
        6.<br>
        7.<br>
        8.<br>
        9.<br>
        10.<br>
        11.<br>
        12.<br>
        13.(VI)<br>
        14.<br>
        15.<br>
        16.<br>
      </p>
    </div>
    <div class="case_box">
      <h3 class="title"></h3>
      <ul class="clear">
        <li v-for="index in 6"><img :src="`static/images/plane/${index}.jpg`" alt=""></li>
      </ul>
    </div>

  </div>
</template>

<script>
  import CommonTitle from '../commonTitle'
  export default{
    components:{
      CommonTitle
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../common/css/mixins";

  .content{
    .title{
      margin-bottom:40px;
    }
    .banner_img{
      margin-bottom:0.4rem;
      img{width:100%;}
    }
    .text{
      font-size: 12px;
      line-height: 24px;
      margin-bottom:40px;
    }
  }
  .type_list{
    margin-bottom:30px;
    h2{
      margin-bottom:40px;
    }
    p{
      line-height:50px;
    }
  }

  .case_box{
    .title{
      margin-bottom:40px;
    }
    ul{
      li{
        float: left;
        width:314px;
        height:190px;
        margin-bottom:35px;
        img{
          width:100%;
        }
        &:nth-child(2n-1){
          margin-right:35px;
        }
      }
    }
  }
</style>



